.body{
    background-image: radial-gradient(#f5f4f9, #e3ecf3);
padding:10px 0;
box-sizing: border-box;
margin-top:20px;

}

.header{
display:flex;
justify-content:space-between;
border:1px solid rgb(226, 198, 198);
box-shadow: 0 15px 40px -20px rgba(40,44,63,.15);
    top: 0;
    left: 0;
    right: 0;
    height: 80px;
    background: #fff;
    z-index: 1000;
    will-change: transform;
    transform: translateZ(0);
    transition: transform .3s ease;
    contain: size layout style;
    padding: 0 20px;
}
article, aside, footer, header, nav, section {
    display: block;
}
*, :after, :before {
    margin: 0;
    padding: 0;
}
.logo img{
    width:26%;
    margin-left:55px;
}
.nav-items>ul{
    display:flex;
    padding: 0 20px;
    

}
.nav-items>ul>li{    
    padding:10px;
    list-style-type:none;
}
.search{
    padding:10px;
}
p{
  font-size:14px;
}
.res-card{
   width:230px;
    margin:5px;
    border-radius:5px;

    padding:2px 4px;
    background-color:#312d2d;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}
.res-card:hover{
    border:1px solid rgba(46, 49, 63, 0.05);
    cursor:pointer;
    
    

}
.res-container{
    display:flex;
    flex-wrap:wrap;
    gap: 15px;
  
}

.res-img{
    width:100%;
    border-radius:10px;

    
}
.res-img:hover{
    transform: scale(1.2); 
    background-color:#6E6E70;
}

h4{
    margin: 0.5em 0 0;
    color: #6E6E70;
    line-height: 1.4em;
  }
  span{
    font-size: 15px;
    margin-right: 4px;
    background-color: #db7c38;
    color: #fff;
  }
  span.dtime{
    margin-left:20px;
    background-color:none;
  }
  .filter-btn{
    margin:26px 0  0 8px;
    border:none;
    border-radius:5px;
    padding:8px;
    color:#db7c38;
    font-weight:bold;
  }
  .search{
    text-align:center;
  }
  
  .nav-items a{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0 10px 30px;
    font-size: 15px;
    color: #555555;
    white-space: nowrap;
    transition: 0.3s;

  }
#btn{
  margin:16px;
  border:none;
    letter-spacing: 0.1px;
    border-radius: 8px;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.625rem;
    padding-bottom: 0.625rem;
    background-color: #ae5a23;
    color: #ffffff;
}

  
  .rounded{
    padding:4.5px;
  }
  .shimmer-card{
    width:230px;
    height: 200px;
    background-color:lightgrey;
    margin:20px;

  }
  .shimmerBG{
    animation-duration: 2.2s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-name: shimmer;
    animation-timing-function: linear;
    background: #ddd;
    background: linear-gradient(to right, #F6F6F6 8%, #F0F0F0 18%, #F6F6F6 33%);

  }
  .title-line {
    height: 24px;
    width: 70%;
    margin-top:120px;
    border-radius: 20px;
}

